.header-box{
    /* padding: 10px 16px; */
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    text-align: center;
    z-index: 2;
    background-color: rgba(185, 179, 119, 0.67);
    width: 100%;
    
}
.header-logo{
    width: 10%;
    height: auto;
    cursor: pointer;
    position:relative;
    
}

.header-tel{
    margin-left: auto;
    margin-right: auto;
    margin-top: 7%;
    background-color: #570F0F;
    width: 80%;
    height: 50%;
    border-style: solid;
    border-width: 2px;    
    color: #FFFFFF;
    border-color: #000000;
    box-shadow: 0px 8px 4px rgba(0, 0, 0, 0.25);
}

.header-book{
    margin-left: auto;
    margin-right: auto;
    margin-top: 7%;
    background-color: #570F0F;
    width: 80%;
    height: 50%;
    border-style: solid;
    border-width: 2px;    
    color: #FFFFFF;
    border-color: #000000;
    cursor: pointer;
    box-shadow: 0px 8px 4px rgba(0, 0, 0, 0.25);
    &:hover{
        box-shadow: none;
        transform: translate(0, 1px);
    }
}



.header-tel-back{
    margin-top: auto;
    margin-bottom: auto;
    width: 10%;
    height: 3em;
    text-align: center;
}

.header-book-back{
    margin-top: auto;
    margin-bottom: auto;
    width: 10%;
    height: 3em;
    text-align: center;
}

.header-sticky {
    position: fixed;
    top: 0;
    width: 100%;
    animation: stick 1ms linear 1 forwards;
  }

.header-sticky > img{
    width: 7%;
    height: auto;
}


@keyframes stick {
    /* from {
        background-color: #FFFFFF;
    } */
    to {
        background-color: rgba(185, 179, 119, 0.67);
        border-bottom: solid 3px black;
        }
}

